var axios = require('axios');
export var exportItems = function (counter) {
    return axios.get(window.location.href, {
        params: {
            _wpnonce: ACP_Export.nonce,
            acp_export_action: 'acp_export_listscreen_export',
            acp_export_counter: counter
        }
    });
};
export var persistExportScreenOption = function (active) {
    var data = new FormData();
    data.set('action', 'acp_export_show_export_button');
    data.set('value', active ? 'true' : '');
    data.set('layout', AC.layout);
    data.set('list_screen', AC.list_screen);
    data.set('_ajax_nonce', AC.ajax_nonce);
    return axios.post(ajaxurl, data);
};
