import ScreenOption from "./table/screen-option";
import { Export } from "./table/export";
import ExportButton from "./table/button";
import { initAcServices } from "AC/helpers/admin-columns";
var AC_SERVICES = initAcServices();
AC_SERVICES.addListener('Table.Ready', function (payload) {
    var Module = new ExportModule();
    var button = document.querySelector('.ac-table-button.-export');
    if (button) {
        Module.registerService('Exporter', new Export(new ExportButton(button)));
    }
    var elScreenOption = document.querySelector('#acp_export_show_export_button');
    if (elScreenOption) {
        var ScreenOptionModule = new ScreenOption(elScreenOption, payload.table.Actions);
        ScreenOptionModule.init();
        Module.registerService('ScreenOption', ScreenOptionModule);
    }
    AC_SERVICES.registerService('Export', Module);
});
var ExportModule = /** @class */ (function () {
    function ExportModule() {
        this.services = {};
    }
    ExportModule.prototype.registerService = function (name, service) {
        this.services[name] = service;
        return this;
    };
    ExportModule.prototype.getService = function (name) {
        return this.hasService(name) ? this.services[name] : null;
    };
    ExportModule.prototype.hasService = function (name) {
        return this.services.hasOwnProperty(name);
    };
    return ExportModule;
}());
