export default Message;
var createDismissButton = function () {
    var element = document.createElement('button');
    element.classList.add('notice-dismiss');
    element.innerHTML = "<span class=\"screen-reader-text\"> " + ACP_Export.i18n.dismiss + "</span>";
    return element;
};
var NumItemsProcessed = '<span class="num-processed"></span>';
var TotalNumItems = "<span class=\"total-num-items\"></span>";
var PercentageProcessed = '<span class="percentage-processed"></span>';
var TplMessage = "\n\t          <div class=\"exporting\">\n\t            <p>\n\t              <span class=\"spinner is-active\"></span>\n\t              " + ACP_Export.i18n.exporting + "\n\t              " + format(ACP_Export.i18n.processed, NumItemsProcessed, TotalNumItems, PercentageProcessed) + "\n\t            </p>\n\t          </div>\n\t          <div class=\"export-completed hidden\">\n\t            <p>\n\t              " + format(ACP_Export.i18n.export_completed, TotalNumItems) + "\n\t              <a class=\"button button-secondary\" data-download>" + ACP_Export.i18n.download_file + "</a>\n\t            </p>\n\t          </div>\n\t      ";
